import React, { Component } from "react";
import perfil from "../../assets/perfil.jpg";
import "./styles.css";

class Perfil extends Component {
    render() {

        return <div id="perfil">
            <div class="description">
                <div class="description name">
                    <h2>JHIOVANE ALEXSANDER RAVE PINHEIRO</h2>
                </div>
                <div class="description about">
                    <div class="des">
                        <div class="icon study"></div>
                        <h4>Estudante de Engenharia da Computação,</h4>
                        <h4>22 anos</h4> 
                    </div>
                    <div class="des">
                        <div class="icon home"></div>
                        <h4>Natural de Itararé - SP</h4>
                    </div>
                    <div class="des">
                        <div class="icon local"></div>
                        <h4>Morando em Cornelio Procopio - PR</h4>
                    </div> 
                </div>
                <div class="description interest">
                    <div class="des">
                        <div class="icon interest"></div>
                        <h4>Interesses</h4>
                    </div>
                    <ul>
                        <li>
                            <p>Desenvolvimento em Hardware</p>
                        </li>
                        <li>
                            <p>Desenvolvimento Web</p>
                        </li>
                        <li>
                            <p>Aplicativos Mobile</p>
                        </li>
                    </ul>
                </div> 
            </div>
            <div class="perfil image">
                <img src={perfil} id="image-perfil" alt="perfil"></img>
                <div class="bt">
                    <button class="btn">CURRICULO</button>
                </div>
                
            </div> 
        </div>;
    }
}
export default Perfil;