import React, { Component } from "react";
import "./styles.css";

class Info extends Component {
    render() {

        return(
        <div id="info">
            <div class="name">
                <h2>Mais informações</h2>
            </div>
            <div class="about">
                <div class="column education">
                    <div class="des">
                        <div class="icon edu"></div>
                        <h4>Educação</h4>
                    </div>
                    <ul>
                        <li>
                            <h5>Ensino médio</h5>
                            <p>2010-2013 - Epaminondas Ferreira Lobo </p>
                        </li>
                        <li>
                            <h5>Graduação</h5>
                            <p>2016-Atual - Universidade Federal do Paraná</p>
                        </li>
                    </ul>
                </div>
                <div class="column exp">
                    <div class="des">
                        <div class="icon experience"></div>
                        <h4>Experiencia</h4>
                    </div>
                    <ul>
                        <li>
                            <h5>2012-2018 - Centro automotivo</h5>
                            <p>Atuei na realização de diagnósticos,manutenção de veiculos
                                diesel, e no auxilio da administração.
                            </p>
                        </li>
                        <li>
                            <h5>Jan-Fev (2019) - CESAR/FCA</h5>
                            <p>Participei do programa Summer Job, onde desenvolvi uma
                                solução para FCA (Fiat Chrysler Automobilies), utilizando
                                tecnicas de machine learning.
                            </p>
                        </li>
                    </ul>

                </div>
            </div>
        </div>);
    }
}
export default Info;