import React, { Component } from "react";
import sgbs from "../../assets/sgbs.png";
import monitora from "../../assets/monitora.png";
import pill from "../../assets/pill.png";
import truco from "../../assets/truco.PNG";
import geovanna from "../../assets/geovanna.PNG";
import "./styles.css";

class Portfolio extends Component {
    render() {
        return(
            <div id="portfolio">
                <div class="title">
                    <h2>PORTFOLIO</h2>
                    <p>Alguns dos projetos que já desenvolvi</p>
                </div>
                <div class="mini">
                    
                        <img src={monitora} id="monitora" alt="aplicativo"></img>
                    
                   
                     <img src={geovanna} id="geovanna" alt="aplicação web"></img>
                   
                   
                        <img src={pill} id="pill" title="PILLS RIGHT" alt="aplicativo"></img>
                    
                        <img src={sgbs} id ="sgbs" alt="Aplicação Desktop"></img>
                   
                        <img src={truco} id="truco" alt="Jogo desktop"></img>
                    
                </div>

            </div>
            );
    }
}
export default Portfolio;